<template>
    <div
        :style="{
            'background-image': `url(${searchIcon})`,
            'background-repeat': `no-repeat`,
            'background-size': `100% auto`
        }"
    >
        <div class="title">
            <el-button
                type="text"
                icon="el-icon-back"
                :style="{
                    color: '#FFF',
                    'font-size': '30px'
                }"
                @click="backHistory()"
            ></el-button>
            <p
                :style="{
                    'padding-left': '30%',
                    'padding-top': '5%'
                }"
            >
                联系我们
            </p>
        </div>
        <div class="flex-v-center">
            <div class="home-base-setting">
                <el-form :model="contactUs" ref="validateForm">
                    <div class="center-body">
                        <el-form-item
                            class="item"
                            label="姓名"
                            prop="name"
                            :rules="[{ required: true, message: '姓名不能为空' }]"
                        >
                            <el-input
                                type="text"
                                size="small"
                                maxLength="100"
                                v-model="contactUs.name"
                                placeholder="请输入姓名"
                                autocomplete="off"
                            ></el-input>
                        </el-form-item>
                        <el-form-item
                            class="item"
                            label="公司名称"
                            prop="companyName"
                            :rules="[{ required: true, message: '公司名称不能为空' }]"
                        >
                            <el-input
                                type="text"
                                size="small"
                                maxLength="100"
                                v-model="contactUs.companyName"
                                placeholder="请输入公司名称"
                                autocomplete="off"
                            ></el-input>
                        </el-form-item>
                        <el-form-item
                            class="item"
                            label="联系方式"
                            prop="contact"
                            :rules="[{ required: true, message: '联系方式不能为空' }]"
                        >
                            <el-input
                                type="text"
                                size="small"
                                maxLength="100"
                                v-model="contactUs.contact"
                                placeholder="请输入联系方式"
                                autocomplete="off"
                            ></el-input>
                        </el-form-item>
                        <el-form-item class="item" label="备注" prop="remark">
                            <el-input
                                type="textarea"
                                class="textarea-font123"
                                rows="3"
                                resize="none"
                                size="small"
                                maxLength="150"
                                v-model="contactUs.remark"
                                placeholder="请输入备注(0-150字内)"
                                autocomplete="off"
                            ></el-input>
                        </el-form-item>
                        <br />
                    </div>
                    <el-form-item style="margin-bottom:2%;text-align:center;margin-top:6%;">
                        <!-- <el-button type="grey" size="small" @click="backHistory()">返回</el-button> -->
                        <el-button
                            type="primary"
                            size="medium"
                            style="width: 300px;height: 40px;border-radius: 100px; border:0;background:-webkit-linear-gradient(left,#33CCFF,#6679FF);
                                    background:-o-linear-gradient(right,#33CCFF,#6679FF);
                                    background:-moz-linear-gradient(right,#33CCFF,#6679FF);
                                    background:linear-gradient(to right,#33CCFF,#6679FF);"
                            @click="submitForm('validateForm', false)"
                            >提交</el-button
                        >
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import { errorHint } from '@/utils/server-wrapper'
import { postJson } from '@/utils/server'
import eventBus from '@/utils/event-bus'
import { getJsonResult } from '@/utils/server-wrapper'

export default {
    data() {
        let contactUs = {
            name: '',
            tenantId: '',
            companyName: '',
            contact: '',
            remark: ''
        }
        return {
            searchIcon: require('@/assets/images/contact-us-backgroud.png'),
            contactUs
        }
    },
    async beforeRouteEnter(to, from, next) {
        console.log('loaded')
        const tenantId = to.query.tenantId
        let contactUs = await getJsonResult(`/wx/contactUs`)
        next(vm => {
            vm.$data.contactUs.tenantId = tenantId
            vm.$data.contactUs.name = contactUs.name
            vm.$data.contactUs.companyName = contactUs.companyName
            vm.$data.contactUs.contact = contactUs.contact
            vm.$data.contactUs.remark = contactUs.remark
        })
    },
    methods: {
        submitForm(formName, submit) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    const data = Object.assign({}, this.contactUs)
                    data.submitReview = submit
                    postJson(`/wx/contactUs/addItem`, data)
                        .then(() => {
                            eventBus.$emit('showMessage', {
                                type: 'success',
                                message: '提交成功',
                                center: true
                            })
                            this.backHistory()
                        })
                        .catch(e => errorHint(e))
                } else {
                    console.log('error submit!!')
                    return false
                }
            })
        },
        backHistory() {
            this.$router.go(-1) //返回上一层
        }
    }
    // async beforeRouteEnter(to, from, next) {
    //     const idisCode = to.query.idisCode
    //     const { code, codeAttr } = await getJsonResult(`/wx/codeInfo?idisCode=${encodeURIComponent(idisCode)}`)
    //     const history = await getJsonResult(`/wx/check-code-history?codeId=${code.id}`)
    //     next(vm => {
    //         vm.$data.code = code
    //         vm.$data.codeAttr = codeAttr
    //         vm.$data.history = history
    //     })
    // }
}
</script>

